'use strict';

/* eslint-disable no-unused-vars */
/* eslint-disable quote-props */
app.map = {
	mapContainer: document.querySelector('#map-block'),
	mapButtonEvents: document.querySelector('.map__button_events'),
	mapEventsList: document.querySelector('.feed_map'),
	mapClose: document.querySelector('.feed__text-block'),
	mapScrollContainer: document.querySelector('.feed_map .feed__list'),
	mapLoading: function mapLoading() {
		if (this.mapContainer) {
			this.initMap();
		}
	},
	initMap: function initMap() {
		var options = {
			coordinates: {
				lat: 46.701747,
				lng: 2.395849
			},
			markerImage: './assets/images/marker.svg',
			styles: [{
				'featureType': 'administrative.country',
				'elementType': 'labels',
				'stylers': [{
					'weight': 1
				}]
			}, {
				'featureType': 'administrative.country',
				'elementType': 'labels.text',
				'stylers': [{
					'color': '#737373'
				}]
			}, {
				'featureType': 'administrative.locality',
				'elementType': 'labels',
				'stylers': [{
					'color': '#999999'
				}, {
					'weight': 0.5
				}]
			}, {
				'featureType': 'administrative.province',
				'elementType': 'labels',
				'stylers': [{
					'color': '#999999'
				}, {
					'weight': 0.5
				}]
			}, {
				'featureType': 'landscape',
				'stylers': [{
					'color': '#333333'
				}]
			}, {
				'featureType': 'poi.park',
				'stylers': [{
					'color': '#333333'
				}]
			}, {
				'featureType': 'road',
				'stylers': [{
					'color': '#484848'
				}]
			}, {
				'featureType': 'water',
				'stylers': [{
					'color': '#1d1d1d'
				}]
			}]
		};
		var map = new google.maps.Map(this.mapContainer, {
			center: options.coordinates,
			zoom: 5,
			disableDefaultUI: false
		});
		var marker = new google.maps.Marker({
			position: options.coordinates,
			map: map,
			animation: google.maps.Animation.DROP,
			icon: options.markerImage
		});
		map.setOptions({ styles: options.styles });
	},
	showEvents: function showEvents() {
		var _this = this;

		if (this.mapButtonEvents) {
			this.mapButtonEvents.addEventListener('click', function () {
				_this.mapEventsList.style = 'transform: translateX(0);';
			});
		}
	},
	closeEvents: function closeEvents() {
		var _this2 = this;

		if (this.mapClose) {
			this.mapClose.addEventListener('click', function () {
				_this2.mapEventsList.style = '';
			});
		}
	},
	eventListControl: function eventListControl() {
		this.showEvents();
		this.closeEvents();
	},
	scrollEventList: function scrollEventList() {
		if (app.map.mapClose) {
			var pageHeight = document.documentElement.clientHeight;
			var eventListHead = app.map.mapClose.clientHeight;
			var navHeight = 46;
			var listHeight = pageHeight - eventListHead - navHeight;
			app.map.mapScrollContainer.style = 'height: ' + listHeight + 'px;';
		}
	},
	scrollEventListResize: function scrollEventListResize() {
		var _this3 = this;

		window.addEventListener('resize', function () {
			_this3.scrollEventList();
			_this3.reloadBaseVh();
		});
	},
	reloadBaseVh: function reloadBaseVh() {
		var vh = window.innerHeight * 0.01;
		document.documentElement.style.setProperty('--vh', vh + 'px');
	}
};