'use strict';

app.sliderServices = {
	name: 'slider',
	description: 'slick slider',
	init: function init(selector, options) {
		var _this = this;

		app.common.initScript('slick.min', 'slick', function () {
			_this.runSlider(selector, options || _this.options);
		});
		app.common.initStyle('slick');
	},
	runSlider: function runSlider(selector, options) {
		var $slider = $(selector || '.slider__slide');
		$slider.slick(options);
	},

	options: {
		slidesToShow: 1,
		slidesToScroll: 1,
		dots: true,
		arrows: false,
		infinite: false,
		cssEase: 'easeInOutCubic',
		speed: 500,
		responsive: [{
			breakpoint: 1600,
			settings: {
				slidesToShow: 1,
				infinite: true
			}
		}, {
			breakpoint: 1300,
			settings: {
				slidesToShow: 1
			}
		}, {
			breakpoint: 991,
			settings: {
				slidesToShow: 1
			}
		}, {
			breakpoint: 767,
			settings: {
				slidesToShow: 1,
				slidesToScroll: 1
			}
		}, {
			breakpoint: 575,
			settings: {
				slidesToShow: 1,
				slidesToScroll: 1,
				arrows: false,
				dots: true
			}
		}]
	}
};